@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Montserrat", sans-serif;
  padding-bottom: 80px;
}

.app {
  background-color: #fefefe;
  height: 100vh;
}

.app__logo {
  margin-top: 2px;
  font-weight: 900;
  font-style: italic;
  color: #222;
}

.app__header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 9999;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #fff;
  padding: 15px 20px 12px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
}

.app__headerImage {
  object-fit: contain;
  max-width: 100px;
}

.app__signup {
  display: flex;
  flex-direction: column;

  text-align: center;
  padding: 10px;
}

.app__signup input,
.app__signup button {
  margin-top: 20px;
}

.app__loginContainer button {
  margin-left: 10px;
}

.app__header button,
.app__signup button,
.imageupload button {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  background-color: #222;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
}

.app__header button:hover,
.app__signup button:hover,
.imageupload button:hover {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  background-color: #444;
  color: #fff;
}

.App__disclaimer {
  font-size: 13px;
  padding: 50px 20px 0;
  text-align: center;
}

.post {
  background-color: #ffffff;
  max-width: 500px;
  /* border: 1px solid #ddd; */
  border-radius: 10px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);

  margin: 20px auto 20px;
}

@media screen and (max-width: 500px) {
  .post {
    border-radius: 0px;
  }
}

.post__header {
  display: flex;
  align-items: center;
  padding: 10px;
}

.post__avatar {
  margin-right: 10px;
}

.post__name {
  font-size: 14px;
}

.post__image {
  width: 100%;
  object-fit: contain;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}

.post__text {
  padding: 10px 10px 15px;
  font-weight: normal;
}
.post__comments {
  display: flex;
  flex-direction: column;
  padding: 0 10px 0;
  /* width: 100vw; */
}

.post__comments p {
  font-size: 13px;
  margin-bottom: 5px;
  line-height: 20px;
  word-break: break-word;
}

.post__comments p:last-child {
  margin-bottom: 15px;
}

.post__commentBox {
  display: flex;
  width: 100%;
}

.post__input {
  font-family: "Montserrat", sans-serif;
  flex: 1 1;
  border: none;
  outline: none;
  padding: 10px;
  border-top: 1px solid #ddd;
  border-bottom-left-radius: 10px;
}

.post__button {
  font-family: "Montserrat", sans-serif;
  flex: 0 1;
  background: #222;
  color: #fff;
  cursor: pointer;
  border: none;
  outline: none;
  padding: 10px 20px;
  border-top: 1px solid #222;
  border-bottom-right-radius: 10px;
  transition: all 0.5s;
}

.post__button:hover {
  background: #555;
}

.post__button:disabled {
  border-top: 1px solid #ddd;
  background: transparent;
  cursor: default;
}

@media screen and (max-width: 500px) {
  .post__input {
    border-bottom-left-radius: 0px;
  }

  .post__button {
    border-bottom-right-radius: 0px;
  }
}

.imageupload__open {
  font-weight: 900;
  display: inline-block;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.4);
  background: #222;
  color: #fff;
  padding: 10px;
  width: 50px;
  height: 50px;
  outline: none;
  border: 0;
  border-radius: 50%;
  position: fixed;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  bottom: 30px;
  text-align: center;
  cursor: pointer;
  transition: all 0.5s ease;
}

.imageupload__open:hover {
  color: #222;
  background: #fff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.4);
}

.imageupload__open:active {
  outline: none;
  border: 0;
}

.imageupload {
  display: flex;
  flex-direction: column;
  width: 300px;
  background: #fff;
  padding: 20px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  outline: none;

  border-radius: 10px;
  margin: 10px auto;
  text-align: center;
  position: fixed;
  bottom: 20px;
  left: 0;
  right: 0;
  transition: all 0.5s ease;
}

.imageupload__progress {
  width: 100%;
}

.imageupload input[type="text"] {
  font-family: "Montserrat", sans-serif;
  font-size: 13px;
  width: 100%;
  color: #666;
  outline: none;
  padding: 10px 20px;
  margin: 8px 0;
  border: 0;
  box-sizing: border-box;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  transition: all 0.5s ease;
}

.imageupload input[type="text"]:focus {
  background: #eee;
}

.imageupload input[type="file"] {
  /* display: none; */
  outline: none;
  margin-bottom: 5px;
  margin-top: 1px;
  font-family: "Montserrat", sans-serif;
  font-size: 13px;
  padding: 10px 30px;
}

